<template>
<div id="app">
    <div class="chat-min" v-if="!chatMainShow" @click="chatMainShow = !chatMainShow">
        <i><img src="./assets/liaotian.png" alt="" ></i>
    </div>
    <transition name="show">
        <div id="chat-main" class="chat-main" v-if="chatMainShow">
            <div v-if="!complete" class="mask">
                <div class="img">
                    <img src="./assets/loade.gif" alt="">
                </div>
            </div>
             <div class="term">
                <menuList></menuList>
            </div>

            <div class="sidebar">
                <card></card>
                <list></list>
            </div>

            <div class="main">
                <div class="windowList">
                    <ul>
                        <li @click.stop="chatMainShow = !chatMainShow">X</li>
                    </ul>
                </div>
                <message></message>
                <TextAre></TextAre>
            </div>
        </div>
    </transition>  
</div>

</template>

<script>
import { mapState } from 'vuex'
import Vue from 'vue'
import vueR from 'vue-resource'

import Card from './components/card'
import menuList from './components/menuList'
import List from './components/list'
import TextAre from './components/text'
import Message from './components/message'


// const Loadmore = require('vue-loadmore').default;
Vue.use(vueR);

export default {
    name: 'app',
    components: {Card, menuList, List, Message, TextAre},
    computed: mapState({
        complete: (state) => {
            return state.events.complete
        }
    }),
    data () {
        return {
            chatMainShow: false,
        }
    },
    created () {
        // 请求人员信息
        this.$http.get('./static/omsIm/demo/json/json.js')
            .then((response) => {
                this.$store.dispatch('initData', response.data);
            })
            .catch(function(response) {
                console.log(response)
            });
        // 请求消息数量
        this.$http.get('./static/omsIm/demo/json/json1.js')
            .then((response) => {
                this.$store.dispatch('acceptMes', response.data);
            })
            .catch(function(response) {
                console.log(response)
            });
    },
    methods: {
        transf () {

        }
    }
}
</script>

<style lang="less" >
body, html {
    height: 100%;
    overflow: hidden;
}
body {
  color: #4d4d4d;
  font: 14px/1.4em 'Helvetica Neue', Helvetica, 'Microsoft Yahei', Arial, sans-serif;
  background: #f5f5f5 url('./assets/bg.jpg') no-repeat center;
  background-size: cover;
  font-smoothing: antialiased;
  width: 100%;
  height: 100%;
}
ul {
    margin: 0;
    padding: 0;
    li {
      list-style-type: none;
    }
}
</style>
<style lang="less" scoped>
.chat-min {
    cursor: pointer;
    position: fixed;
    z-index: 999999;
    bottom: 28px;
    right:28px;
}
.show-enter-active {
    transition: 0.8s;
    transform: scale(1,1);
    opacity: 1;
}
.show-enter, .show-leave-active{
    transition: 0.5s;
    opacity: 0;
    transform: scale(0.01,0.01);
    bottom: 29px; 
    right: 29px;
}

.windowList{
    height: 5px;
    ul {
        float: right;
        li {
            padding: 10px 15px;
            font-size: 20px;
            cursor: pointer;
        }
    }
}
#chat-main {
    width: 860px;
    height: 600px;
    position: fixed;
    top: 20px;
    left: 50%;
    margin-left: -430px;
    border-radius: 3px;
    .mask {
        background-color: rgba(255,255,255,0.8);
        position: fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        z-index:999999;
        .img {
            position: relative;
            top:50%;
            margin-top:-16px;
            text-align:center;
            vertical-align:middle;
            width:32px;
            line-height:100%;
            height:100%;
            margin:auto;
        }
    }
    .sidebar, .main, .term {
        height: 100%;
    }
    .term {
        float: left;
        color: #f4f4f4;
        background-color: #2e3238;
    }
    .sidebar {
        float: left;
        width: 200px;
        color: #f4f4f4;
        background-color:#2a5431;
    }
    .main {
        position: relative;
        overflow: hidden;
        background-color: #eee;
    }
    .text {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
    }
    .message {
        height: ~'calc(100% - 160px)';
    }
    ul {
      margin: 0;
      padding: 0;
    }
}
</style>




