<script>
import { mapState } from 'vuex';
import Vue from 'vue';

export default {
    data  () {
        return {
            mousedownShow: false,
            mousedowInfo: {id: 1, index: 1}
        }
    },
    methods: {
        selectSession (id, type, name, img) {
            let data = {id: id, type: type, name: name, img: img};
            this.$store.dispatch('selectSession', data);
        },
        mousedown (i, index, e)  {
            if ( e.which == 3 ) {
                e.sta
                this.mousedownShow = true;
                this.$nextTick( () => {
                    this.$refs.recentContactsDel.style.top = e.pageY + 2 + 'px';
                    this.$refs.recentContactsDel.style.left = e.pageX + 2 + 'px';
                })
                this.mousedowInfo.id = i.id;
                this.mousedowInfo.index = index;
            };
            // console.log(i);
            // console.log(index);
            // console.log(event);
        },
        removeZjlxr ()  {
            // console.log(this.mousedowInfo.id);
            this.$store.dispatch('delSession', this.mousedowInfo.index);
        }
    },
    computed: mapState({
        currentId: (state) => {
            return state.currentSession.id
        },
        // sessions: (state) => {
        //     let result = state.sessions.filter(session => session.user.name.includes(state.filterKey));
        //     return result;
        // },
        sessions: (state) => {
            return state.sessions

        },
        searchFriend: (state) => {
            if ( state.filterKey == "" ) {return [];};
            let result = [];
            for (var i in state.friends) {
                if (state.friends[i].list) {
                    result = result.concat(state.friends[i].list.filter(friend => friend.username.includes(state.filterKey)))
                };
            }
            
            return result;
        },
        indexTab: (state) => state.indexTab,
        friends: (state)=> state.friends,
        group: (state)=> state.group,
    }),
    created () {
        document.addEventListener('click', (e)=> {
            if (this.mousedownShow) {
                this.mousedownShow = !this.mousedownShow;
            };
        })
    }
};
Vue.directive('oncontextmenu', {
    bind: function (el) {
        el.oncontextmenu = function () {
            return false;
        }
    }
})
</script>

<template>
<div class="list">
    <div class="rcDel" v-if="mousedownShow" ref ="recentContactsDel">
        <ul>
            <li @click="removeZjlxr()">移除该会话</li>
        </ul>
    </div>
    <div class="search"  v-if="searchFriend.length != 0">
         <ul>
            <li v-for="item in searchFriend" :class="{ active: item.id === currentId }" @click="selectSession(item.id, 'message', item.username, item.avatar)">
                <img class="avatar"  width="30" height="30" :alt="item.username" :src="'./static' + item.avatar">
                <p class="name">{{item.username}}</p>
            </li>
        </ul>
    </div>

    <div v-if = "1 == indexTab">

        <ul id="zjlxr" v-oncontextmenu ="sessions">
            <li v-for="(item, index) in sessions" :id= "currentId" :item="item.id" :class="{ active: item.id === currentId }" @click="selectSession(item.id, item.type, item.user.name, item.user.img)" @mousedown.3 ="mousedown(item, index, $event)">
                <img class="avatar"  width="30" height="30" :alt="item.user.name" :src="item.user.img">
                <p class="name">{{item.user.name}}</p>
                <span class="message-num" v-if="item.messageNum" >{{ item.messageNum }}</span>
            </li>
        </ul>
    </div>
    <div v-if = "2 == indexTab">
        <ul class="friend-list">
            <li v-for="item in friends" class="">
                <p>{{ item.groupname }}</p>
                <ul>
                    <li v-for="list in item.list"  @click="selectSession(list.id, 'message', list.username, './static' + list.avatar)" :class="{ active: list.id === currentId }">
                        <img :src="'/static/' + list.avatar" alt="" width="30" height="30">
                        <p class="name">{{ list.username }}</p>
                    </li>
                </ul>
                <!-- <img class="avatar"  width="30" height="30" >
                <p class="name">{{'测试'}}</p> -->
            </li>
        </ul>
    </div>
    <div v-if = "3 == indexTab">
         <ul class="group-list">
            <li v-for="item in group" class="" @click="selectSession(item.id, 'groupMessage', item.groupname, './static' + item.avatar)">
                <img class="avatar" :src="'./static' + item.avatar"   width="30" height="30" >
                <p class="name">{{ item.groupname }}</p>
            </li>
        </ul>
    </div>
</div>
</template>

<style scoped lang="less" >
.list {
    .rcDel {
        position: absolute;
        width: 150px;
        box-shadow: 0 0 4px #ccc;
        background-color: #fff;
        z-index: 999;
        color: #000;
        li {
            padding-left: 15px;
        }
    }
    .search { 
        position: absolute;background-color: #2a5431;width: 200px;height: 530px;overflow:auto;
        z-index: 1;
         &::-webkit-scrollbar{width:100px ;display: block;}
        &::-webkit-scrollbar{width:10px;height:10px}
        &::-webkit-scrollbar-track{background:rgba(0,0,0,0)}
        &::-webkit-scrollbar-thumb{background:rgba(0,0,0,0.5);border-radius:10px}
        &::-webkit-scrollbar-corner{background:#82afff}
        &::-webkit-scrollbar-resizer{background:#ff0bee}
        &::scrollbar{width:100px ;display: block;}
        &::scrollbar{width:10px;height:10px}

        &::scrollbar-track{background:rgba(0,0,0,0)}
        &::scrollbar-thumb{background:rgba(0,0,0,0.5);border-radius:10px}
        &::scrollbar-corner{background:#82afff}
        &::scrollbar-resizer{background:#ff0bee}
    }
    .friend-list {
        height: 530px;
        overflow:auto;
        &::-webkit-scrollbar{width:100px ;display: block;}
        &::-webkit-scrollbar{width:10px;height:10px}
        &::-webkit-scrollbar-track{background:rgba(0,0,0,0)}
        &::-webkit-scrollbar-thumb{background:rgba(0,0,0,0.5);border-radius:10px}
        &::-webkit-scrollbar-corner{background:#82afff}
        &::-webkit-scrollbar-resizer{background:#ff0bee}
        &::scrollbar{width:100px ;display: block;}
        &::scrollbar{width:10px;height:10px}

        &::scrollbar-track{background:rgba(0,0,0,0)}
        &::scrollbar-thumb{background:rgba(0,0,0,0.5);border-radius:10px}
        &::scrollbar-corner{background:#82afff}
        &::scrollbar-resizer{background:#ff0bee}
    }
    li {
        padding: 12px 0 12px 8px;
        cursor: pointer;
        transition: background-color .1s;
        position: relative;
        &:hover {
            background-color: rgba(255, 255, 255, 0.03);
        }
        &.active {
            background-color: rgba(255, 255, 255, 0.1);
        }
        .message-num {
            position: absolute;
            display: line-block;
            width: 20px;
            height: 20px;
            line-height: 20px;
            border-radius: 20px;
            background-color: #d32f2f;
            color: #fff;
            text-align: center;
            right: 10px;
        }
    }
    .avatar, .name {
        vertical-align: middle;
    }
    .avatar {
        border-radius: 2px;
    }
    .name {
        display: inline-block;
        margin: 0 0 0 15px;
    }
}
</style>


