
<script>
import { mapState } from 'vuex'
export default {
    data: function () {
        return {
            selfInfo: false,
            setShow: false,
            sets: {
                voice: [
                    '开启声音',
                    '关闭声音'
                ],
                desktop: [
                    '开启桌面通知',
                    '关闭桌面通知'
                ]
            }
        };
    },
    computed: mapState({
        user: ({ user }) => user,
        filterKey: ({ filterKey }) => filterKey,
        indexTab: ({ indexTab }) => indexTab,
        userSet: ({ userSet }) => userSet,
    }),
    // name: 'menu',
    // data: function () {
    // },
	// vuex: {
 //        actions: actions,
 //        getters: {
 //            user: ({ user }) => user,
 //            filterKey: ({ filterKey }) => filterKey,
 //            indexTab: ({ indexTab }) => indexTab,
 //        },
 //    },
    methods: {
        selectTab: function (num) {
            this.$store.dispatch('selectTab', num);
        },
        showInfo: function (e) {
            if ( !this.selfInfo ) {
                this.selfInfo = !this.selfInfo;
                
            };
        },
        setUser (i) {
            let userSet = this.userSet;
            userSet[i] = userSet[i] == 0 ? 1 : 0;
            this.$store.dispatch('userSet', i, userSet[i]);
            localStorage.setItem('chat-set', JSON.stringify(userSet));
        }
    },
    created () {
        document.addEventListener('click', (e) => {
            if (  this.selfInfo ) {
                 this.selfInfo = !this.selfInfo;
             }
             if ( this.setShow ) {
                 this.setShow = !this.setShow;
             };
        })
    }
}

</script>
<template>
	<div class="menu">
		<ul>
			<li style="position: relative" @click.stop="showInfo()" ><img :src="user.img" width="40px" height="40px" alt="">
                <div class="selfInfo" ref="si" v-if="selfInfo">
                    <div>
                        <span>{{ user.name }}</span>
                    </div>
                    <div>
                        <!-- <span>职位：</span> -->
                    </div>
                </div>
            </li>
            <li :class="{actionColor:1 ==  indexTab}"><img src="../assets/zjlxr2.png"   @click="selectTab(1)" alt="" width="40px" height="40px"></li>
            <li :class="{actionColor:2 ==  indexTab}"><img src="../assets/lxr1.png" @click="selectTab(2)" alt="" width="30px" height="30px"></li>
			<li :class="{actionColor:3 ==  indexTab}"><img src="../assets/q.png" @click="selectTab(3)" alt="" width="30px" height="30px"></li>
		</ul>
        <div class="chat-set" title="设置">
            <i @click.stop="setShow = !setShow"><img src="../assets/liebiao.png" alt=""></i>
            <transition name="fade">
                <ul class="chat-set-list" v-if="setShow">
                    <li>新建群聊</li>
                    <li v-for="(set, index) in sets" @click.stop="setUser(index)">{{set[userSet[index]]}}</li>
                </ul>
             </transition>
        </div>
	</div>
</template>
<style scoped lang="less">
    .fade-enter-active, .fade-leave-active {
      transition: opacity .5s
    }
    .fade-enter, .fade-leave-active {
      opacity: 0
    }
	.menu {
        position: relative;
        cursor:pointer; 
		width: 60px;
        height: 100%;
        li {
            padding: 10px;
        }
        .actionColor { 
            background-color: rgba(228, 219, 219, 0.3);
        }
        .selfInfo {
            position: absolute;
            width: 200px;
            height: 200px;
            top: 50px;
            left: 50px;
            z-index: 1000;
            color: #000;
            background-color: #fff;
        }
        .selfInfo > div {
            padding: 20px;
            text-align: left;
        }
        .selfInfo > div > span{
            font-size: 20px;
        }
        .chat-set {
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 50px;
            .chat-set-list {
                position: absolute;
                text-align: left;
                left: 10px;
                bottom: 100%;
                width: 160px;
                color: #000;
                background-color: #fff;
                box-shadow: 0 6px 12px rgba(0,0,0,.175);
                border-radius: 5px;
                z-index: 99;
            }
        }
        .chat-set i{
            width: 100%;
            height: 50px;
            display: block;
            text-align: center;

        }
	}
</style>


