<script>
import { mapState } from 'vuex';
import insertAtCursor from '../common/insertAtCursor';
import qn from '../common/QiniuUpload';
import emojis from '../common/emojis';
export default {
    data() {
        return {
            content: '',
            selShow: false,
            currentSel: 1, 
            emShow: false,
            emojis: emojis
        };
    },
    computed: mapState({
        start: (state) => {
            return state.currentSession.id;
        }
    }),
    methods: {
        onKeydown (e) {
            if ( this.currentSel == 0 ) {
                if ( e.ctrlKey && e.keyCode === 13 && this.content.length ) {
                    this.send();
                }
            } else {
                if ( e.shiftKey || e.ctrlKey ) {
                    return ;
                };
                if ( e.keyCode === 13  && this.content.length ) {
                        e.preventDefault();
                        this.send();
                } 
            }
        },
        send () {
            let data = {
                content: this.content
            };
            this.content = '';
            this.$store.dispatch('sendMessage', data);
        },
        changeSend (cur) {
            this.currentSel = cur;
            localStorage.setItem('currentSel', cur);
        },
        emojiInsert ( i ) {
            insertAtCursor(this.$refs.textarea, '【'+i.name+'】');
            this.content = this.$refs.textarea.value;
        },
        fileUpload (e) {
            let file = '';
            if (typeof e.target === 'undefined') file = e[0];
            else file = e.target.files[0];
            if (typeof file == 'undefined' ) {
                return false;
            };

            if(/image\/\w+/.test(file.type)){  
                console.log('img');
            } else {
                console.log('noimg');
            }

            let size = Math.floor(file.size / 1024);
            qn.Qiniu_upload({
                    key: '/44/44/44/44/4',
                    tokenurl: '/uptoken.php',
                    f: file,
                    QiniuUrl: 'http://up.qiniu.com',
                }, function (data) {
                    if ( !data.error ) {
                        console.log(data);
                    } else {
                        console.log(4444);
                    }
                });
            // 双向绑定
            // this.$emit('input', this.file);
        }
    },
    created () {
        let data = localStorage.getItem('currentSel');
        document.addEventListener('click', (e)=> {
            if (this.emShow) {
                this.emShow = !this.emShow;
            };
            if ( this.selShow ) {
                this.selShow = !this.selShow;
            };
        })
        if ( data != null ) {
            this.currentSel = parseInt(data);
        };
    }
};
</script>

<template>
<div class="text"  v-if="start">
<!--  表情包 -->
    <div class="chat-tool">
        <div class="emoticon" v-if="emShow">
            <ul class="emoticon-list">
                <li @click.stop = "emojiInsert(emojiObj)" v-for="(emojiObj, index) in emojis">
                    <img :src="'/static/emoji/'+ emojiObj.num +'@2x.png'" :title="emojiObj.name" alt="">
                </li>
            </ul>
        </div>
        <ul>
       <!--  // 工具  表情 -->
            <li @click.stop="emShow = true"><img src="../assets/biaoqing.png" alt=""></li>
        <!-- 文件上传  -->
            <li><label for="chat-file"><img src="../assets/wenjian.png" alt=""></label><input type="file" id="chat-file" style="display: none" @change="fileUpload($event)"></li>
        </ul>
    </div>
    <textarea placeholder="" v-model="content" ref="textarea" @keydown.enter="onKeydown"></textarea>
    <div class="send-act">
        <div class="text-right">
            <span class="send-btn btn btn-default" @click="send()">发送</span>
            <div class="send-select-box" @click.stop="selShow = !selShow">
                <span class="send-select"></span>
            </div>
            <ul class="send-select-list" ref="ssl" v-if="selShow">
                <li :class="[ currentSel == 1 ? 'active': ''  ]" @click="changeSend(1)">按Enter 键发送</li>
                <li :class="[ currentSel == 1 ? '': 'active'  ]" @click="changeSend(0)">按Ctrl + Enter 键发送</li>
            </ul>
        </div>
    </div>
</div>
</template>

<style lang="less" scoped>
.chat-tool {
    height: 32px;background-color: #fff;
    // 表情包
    .emoticon {
        position: absolute;
        bottom: 100%;
        background-color: #fff;
        box-shadow: 0 0 10px #ccc;
    }
    ul{
         height: 100%;
         padding-left: 10px;
    }
    ul li{
        float:left;
        width: 34px;
        height: 100%;
        text-align: center;
        line-height: 32px;
    }
    li img{
        width: 20px;
        height: 20px;
        cursor: pointer
    }
    li label {
        width: 100%;height: 100%;
    }
}
.text {
    height: 160px;
    border-top: solid 1px #ddd;

    textarea {
        padding: 2px 10px;
        height:  95px;
        width: 100%;
        border: none;
        outline: none;
        font-family: "Micrsofot Yahei";
        resize: none;
        margin:0;
        border-bottom: 1px solid #ccc;
    }
    .send-act {
        position: relative;
        top:-7px;
        background-color: #fff;
    }
    .send-btn {
        padding: 6px 26px;
        border-radius: 0;
         margin: 0 30px;
    }
    .active {
        border-left: 8px solid #88154c;
    }
    .send-select-box {

        display: inline-block;
        position: absolute;
        height: 34px;
        width: 20px;
        right: 10px;
        border: 1px solid #ccc;
        cursor: pointer;
        .send-select{
            border-top: 8px solid #955F34;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            position: absolute;
            top: 12px;
            right: 4px;
        }
    }
    .send-select-list {
        position: absolute;
        border: 1px solid #E2E2E2;
        border-radius: 2px;
        background-color: #fff;
        box-shadow: 1px 1px 20px rgba(0,0,0,.1);
        left: auto;
        right: 0;
        bottom: 100%;
        width: 180px;
        padding: 10px 0;

        li {
            cursor: pointer;
            padding-right: 15px;
            line-height: 28px;
            &:hover {
                background-color: #ccc
            }
        }

    }
}
</style>


